window.homepageLink = () => {
    let link;
    if (window.location.host.includes("-preview")) {
        link = "https://presearch-preview.org";
    } else if (window.location.host.includes("localhost")) {
        link = "/";
    } else {
        link = "https://presearch.org";
    }
    return {
        state: {
            link,
        },
    };
};