import Cookie from "js-cookie";

window.settings = (PRESEARCH_DOMAIN) => {
    if (window.location.host.includes("localhost")) {
        PRESEARCH_DOMAIN = "localhost";
    }
    const showProviders = localStorage.getItem("showProviders");
    return {
        state: {
            settingsInfoVisible: false,
            localSearchDisabled: Cookie.get("disableLocalSearch") || false,
            localSearchInfoVisible: false,
            darkModeEnabled:
                document.cookie.includes("screenMode=dark") || (!document.cookie.includes("screenMode") && window.matchMedia("(prefers-color-scheme: dark)").matches) || false,
            darkModeInfoVisible: false,
            homepageLink: window.homepageLink().state.link,
            alwaysShowProviders: showProviders ? JSON.parse(showProviders) : true,
        },
        init() {
            Alpine.store("showProviders", {show:this.state.alwaysShowProviders});
        },
        openSettings() {
            this.state.settingsInfoVisible = true;
        },
        closeSettings(shareVisible) {
            if(!shareVisible) this.state.settingsInfoVisible = false;
        },
        disableLocalSearch(el) {
            el.children[0].classList.remove("right-0");
            el.children[1].classList.remove("bg-presearch-alternative");
            this.state.localSearchDisabled = true;
            this.state.localSearchInfoVisible = true;
            Cookie.set("disableLocalSearch", true, { expires: 365, sameSite: "Lax" });
        },
        enableLocalSearch(el) {
            el.children[0].classList.add("right-0");
            el.children[1].classList.add("bg-presearch-alternative");
            this.state.localSearchDisabled = false;
            this.state.localSearchInfoVisible = true;
            Cookie.remove("disableLocalSearch");
        },
        enableDarkMode() {
            document.querySelector("html").classList.add("dark");
            this.state.darkModeEnabled = true;
            this.state.darkModeInfoVisible = true;
            Cookie.set("screenMode", "dark", { domain: PRESEARCH_DOMAIN.split("https://").join(""), path: "/", expires: 365, sameSite: "Lax" });
        },
        disableDarkMode() {
            document.querySelector("html").classList.remove("dark");
            this.state.darkModeEnabled = false;
            this.state.darkModeInfoVisible = true;
            Cookie.set("screenMode", "light", { domain: PRESEARCH_DOMAIN.split("https://").join(""), path: "/", expires: 365, sameSite: "Lax" });
        },
        alwaysShowProviders(el, value) {
            if (value) {
                Alpine.store("showProviders", {show:true});
                localStorage.setItem("showProviders", JSON.stringify(true))
                el.children[0].classList.add("right-0");
                el.children[1].classList.add("bg-presearch-alternative");
                this.state.alwaysShowProviders = true;
            } else {
                Alpine.store("showProviders", {show:false});
                localStorage.setItem("showProviders", JSON.stringify(false))
                el.children[0].classList.remove("right-0");
                el.children[1].classList.remove("bg-presearch-alternative");
                this.state.alwaysShowProviders = false;
            }
        },
    };
};
