import { io } from "socket.io-client";

window.searchResults = () => {
    const lastSearchId = localStorage.getItem("lastSearchId");
    const cachedResults = localStorage.getItem("results");
    return {
        state: {
            results: {
                standardResults: [],
            },
        },
        init() {
            const socket = io.connect();

            // use cached results, when user is getting to the results page by pressing back button
            if (lastSearchId == window.searchId) {
                const data = JSON.parse(cachedResults);
                this.state = data;
            }

            socket.on("connect", () => {
                // send search id to web server backend to map search id with socket connection id
                socket.emit("setId", window.searchId);
                const timeoutError = setTimeout(() => {
                    if (!this.state.response) this.setError();
                }, 8000);
            });

            socket.on("searchResults", (data) => {
                const { error } = data;
                if (error) {
                    console.log("got an error", error);
                    data.response = true;
                    data.results = { error };
                    return (this.state = data);
                }
                this.setResults(data);
            });
        },
        setResults(data) {
            if (data.results && window.location.pathname == "/images") {
                data.results.pages = 6;
            }
            if (data.results && data.results.pages) {
                let numberOfPages = data.results.pages;
                data.results.pages = [];
                for (let i = 1; i <= numberOfPages; i++) {
                    if (i <= 6) data.results.pages.push(i);
                }
            }
            if (data.images) Alpine.store("images", data.images);

            // save and remove javascript from presearch package
            if (data.presearchPackage && data.presearchPackage.html) {
                const scriptHtml = data.presearchPackage.html.match(/<script[\s\S]*?>[\s\S]*?<\/script>/gi);
                if (scriptHtml && scriptHtml.length) {
                    data.presearchPackage.html = data.presearchPackage.html.split(/<script[\s\S]*?>[\s\S]*?<\/script>/gi).join("");
                    data.presearchPackageJs = scriptHtml[0].split(/<script>|<\/script>/gi).join("");
                }
            }
            data.response = true;
            this.state = data;
            localStorage.setItem("results", JSON.stringify(data));
            localStorage.setItem("lastSearchId", window.searchId)
        },
        injectJs(el) {
            if (!this.state.presearchPackageJs) return;
            let script = document.createElement("script");
            script.type = "text/javascript";
            let code = this.state.presearchPackageJs;
            try {
                script.appendChild(document.createTextNode(code));
                el.appendChild(script);
            } catch (e) {
                script.text = code;
                el.appendChild(script);
            }
        },
        setError() {
            let data = { results: { error: { code: 503, message: "No response from the gateway" } } };
            this.state = data;
        },
        generateLink(link) {
            // remove https:// and last / from the link to make it looks nicer
            let cleanLink = link.split("://")[1];
            if (cleanLink[cleanLink.length - 1] === "/") cleanLink = cleanLink.substr(0, cleanLink.length - 1);
            return cleanLink.length > 59 ? cleanLink.substring(0, 59) + "..." : cleanLink;
        },
    };
};
