import defaultProviders from "../json/defaultProviders.json";
import translations from "../json/translations.json";
import urldecode from "locutus/php/url/urldecode";
import decodeHtml from "decode-html";
import Cookie from "js-cookie";
import axios from "axios";

window.userInfo = (PRESEARCH_DOMAIN, query, lang) => {
    const getSavedProviders = localStorage.getItem("providers");
    const getSavedBalance = localStorage.getItem("balance");
    const getSavedToken = Cookie.get("token");
    let translated;
    let filtered = translations.filter((el) => el.language_code.toLocaleLowerCase() == lang.toLocaleLowerCase());
    if (!filtered.length) filtered = translations.filter((el) => el.language_code.split("-")[0] == lang.toLocaleLowerCase());

    if (filtered.length) {
        const { all, images, news, videos, share } = filtered[0];
        translated = { all, images, news, videos, share };
    } else {
        translated = { all: "All", images: "Images", videos: "Videos", news: "News", share: "Share" };
    }
    return {
        user: {
            loading: true,
            loggedIn: false,
            providers: getSavedProviders ? JSON.parse(getSavedProviders) : defaultProviders, // need defaults in case api call fails
            balance: getSavedBalance ? JSON.parse(getSavedBalance) : null,
            announcements: [],
            PRESEARCH_DOMAIN: PRESEARCH_DOMAIN,
            query,
            shareVisible: false,
            settingsVisible: false,
        },
        colors: {
            index: 0,
            lastIndex: 0,
            value: ["text-yellow-400", "text-green-400", "text-blue-400", "text-purple-400"],
        },
        translated: translated, 
        init() {
            if (!getSavedToken) {
                return this.error();
            }
            axios
                .get(`${PRESEARCH_DOMAIN}/api/dsearch/user-info`, {
                    headers: {
                        Authorization: "Bearer " + getSavedToken,
                    },
                })
                .then((res) => {
                    this.saveUserInfo(res);
                })
                .catch((err) => {
                    this.error(err);
                });
        },
        decodeSVG(encodedSVG) {
            let decodedSVG = urldecode(encodedSVG);
            if (decodedSVG.includes("<style")) decodedSVG = decodedSVG.split(/<style[^>]*>[^>]*>/gi).join("");
            return decodedSVG;
        },
        decodeHtml(encodedHtml) {
            return decodeHtml(encodedHtml);
        },
        saveProviders(providers) {
            return localStorage.setItem("providers", JSON.stringify(providers));
        },
        saveBalance(balance) {
            return localStorage.setItem("balance", JSON.stringify(balance));
        },
        iconClass(classes) {
            return `<i class="fa ${classes}" />`;
        },
        filterProviders(providers) {
            return providers.filter((provider) => {
                if (provider.id == 25038 && !window.location.host.includes("testnet")) {
                    return false;
                } else if (provider.id == 25039 && window.location.host.includes("testnet")) {
                    return false;
                } else {
                    return true;
                }
            });
        },
        getCurrentUrl() {
            return encodeURI(window.location.href);
        },
        saveUserInfo(res) {
            if (!res.data) return this.error(false);
            if (res.data && !res.data.success) {
                localStorage.removeItem("providers");
                localStorage.removeItem("balance");
                Cookie.remove("token");
            }
            if (res.data.providers) {
                // filter providers (remove engine icon on engine, testnet on testnet)
                res.data.providers = this.filterProviders(res.data.providers);
                // save providers to local storage
                this.saveProviders(res.data.providers);
            }
            if (res.data.balance) this.saveBalance(res.data.balance);

            this.user.loading = false;
            this.user.loggedIn = res.data.success || false;
            this.user.providers = res.data.providers || defaultProviders;
            this.user.balance = res.data.balance || null;
            this.user.announcements = res.data.announcements || [];
            this.user.PRESEARCH_DOMAIN = PRESEARCH_DOMAIN;
            this.user.refferalId = res.data.referral_id;
            this.user.query = query;

            if (res.data.jwt) Cookie.set("token", res.data.jwt, { expires: 365, sameSite: "Lax" });

            if (this.user.loggedIn && ["/login"].includes(window.location.pathname)) {
                window.location.href = "/";
            }
        },
        error(connectionError) {
            if (connectionError) return;
            this.user.loading = false;
            this.user.loggedIn = false;
            this.user.providers = defaultProviders;
            this.user.announcements = [];
            this.user.PRESEARCH_DOMAIN = PRESEARCH_DOMAIN;
            this.user.query = query;
            this.user.balance = null;

            localStorage.removeItem("providers");
            localStorage.removeItem("balance");
            Cookie.remove("token");
        },
        authorizeUser() {
            this.listenForAuthorization();
            window.authWindow = window.open(`${PRESEARCH_DOMAIN}/external-login?`, "_blank", "width=600,height=600");
        },
        copy(text) {
            let input = document.createElement("input");
            input.setAttribute("value", text);
            document.body.appendChild(input);
            input.select();
            let result = document.execCommand("copy");
            document.body.removeChild(input);
            document.querySelector(".copyText").innerHTML = "Copied!";
            setTimeout(() => {
                document.querySelector(".copyText").innerHTML = "Copy Link";
            }, 800);
            return result;
        },
        getLastClassBasedIconIndex() {
            let lastIndex = 0;
            this.user.providers.forEach((provider, index) => provider.icon.type == "class" ? lastIndex = index : '');
            return lastIndex;
        },
        getIconColor(lastIndex) {
            if (this.colors.index > this.colors.value.length - 1) this.colors.index = 0;
            const color = this.colors.value[this.colors.index];
            this.colors.index++;
            if (lastIndex == this.getLastClassBasedIconIndex()) this.colors.index = 0;
            return color;
        },
        showShare() {
            this.user.shareVisible = true;
        },
        hideShare() {
            this.user.shareVisible = false;
        },
        showSettings() {
            this.user.settingsVisible = true;
        },
        hideSettings() {
            this.user.settingsVisible = false;
        },
        listenForAuthorization() {
            window.addEventListener("message", (msg) => {
                if (msg.data.userInfo) {
                    if (msg.data.token) {
                        Cookie.set("token", msg.data.userInfo.jwt, { expires: 365, sameSite: "Lax" });
                    }
                    const userInfo = { data: msg.data.userInfo };
                    this.saveUserInfo(userInfo);
                    window.authWindow.close();
                }
            });
        },
        dismissAnnouncement() {
            this.user.announcements.shift();
            axios
                .get(`${PRESEARCH_DOMAIN}/api/user/update-announcements`, {
                    headers: {
                        Authorization: "Bearer " + getSavedToken,
                    },
                })
                .catch((err) => {
                    console.log(err);
                });
        },
    };
};
