import queryString from "query-string";

const urlParams = queryString.parse(window.location.search);

if (urlParams.rid) {
    // set referral cookie for presearch.org
    const partialDomain = window.location.host.split(".");
    const domain = partialDomain.length === 3 ? `${partialDomain[1]}.${partialDomain[2]}` : window.location.hostname;
    Cookie.set("refid", urlParams.rid, { domain, path: "/" });
}