window.searchForm = () => {
    const input = document.querySelector("input");
    return {
        state: {
            clearButton: false,
        },
        init() {
            if (input.value) this.state.clearButton = true;
            input.addEventListener("input", () => {
                if (input.value) this.state.clearButton = true;
                else this.state.clearButton = false;
            });
        },
        clearButtonPress() {
            input.value = "";
            this.state.clearButton = false;
            input.focus();
        },
    };
};
