// handle image display on "Images" tabs
window.gallery = (images) => {
    return {
        state: {
            visible: false,
        },
        showGallery(i) {
            window.location.hash = "#view";
            this.state = {
                image: images[i].image,
                link: images[i].link,
                title: images[i].title ? images[i].title.split("...").join("") : "",
                visible: true,
                currentIndex: i,
            };
        },
        hideGallery() {
            this.state = {
                visible: false,
                image: "",
                link: "",
                title: "",
            };
        },
        nextImage() {
            window.getSelection().removeAllRanges();
            let i = this.state.currentIndex + 1;
            if (i > images.length - 1) i = 0;
            this.state.image = images[i].image;
            this.state.link = images[i].link;
            this.state.title = images[i].title ? images[i].title.split("...").join("") : "";
            this.state.currentIndex = i;
        },
        prevImage() {
            window.getSelection().removeAllRanges();
            let i = this.state.currentIndex - 1;
            if (i < 0) i = images.length - 1;
            this.state.image = images[i].image;
            this.state.link = images[i].link;
            this.state.title = images[i].title ? images[i].title.split("...").join("") : "";
            this.state.currentIndex = i;
        },
        onImageLoad(e) {
            this.state.width = e.target.naturalWidth;
            this.state.height = e.target.naturalHeight;
        },
        backgroundClick() {
            this.hideGallery();
        },
        keydown(e) {
            if (e.code === "ArrowLeft") {
                this.prevImage();
            } else if (e.code === "ArrowRight") {
                this.nextImage();
            }
        },
    };
};