import Cookie from "js-cookie";

// Reset "b" cookie to initial value. If user will hit the server with this value,
// this search will be rewarded
Cookie.set("b", 0, { secure: true });

let userHasTypedQuery = false;

window.onbeforeunload = () => {
    // if user is on the search page and he didn't typed a new query, set cookie to 1 just before a new request
    // to disable reward for this request. Without that, user will get a reward, when he/she will refresh the page.
    // note: search request with 'page' query are already blocked on the backend
    if (window.location.search && !window.location.search.includes("&page=") && !userHasTypedQuery) {
        Cookie.set("b", 1, { secure: true });
    }
};

document.querySelector("input").addEventListener("input", () => (userHasTypedQuery = true));
